import { Box, Center } from "@chakra-ui/react";
import React from "react";
import styles from "../Styles/Skills.module.css";
import Aos from 'aos';
import 'aos/dist/aos.css';                                                          
import Tools from "./Tools";

const Skills = () => {
    React.useEffect(()=> {
        Aos.init({duration: 1000})
    },[])

  return (
    <div className={styles.bg} id="skills">
    <div className={styles.skill}>
      <Center m="auto" data-aos="zoom-in" fontSize={{base:"25px",sm:"25x",md:"30px",lg:"40px"}} fontWeight="bold" borderBottom="solid 4px rgba(255, 115, 0, 0.801)" paddingBottom="0px" w={{base:"130px",sm:"130px",md:"180px",lg:"220px"}}>MY SKILLS</Center>
      <div className={styles.SkillGrid}>
        <Box data-aos="zoom-in-left">
          <img alt="img" src="https://chiranjeev-thapliyal.vercel.app/svg/html-5.svg" />
          <h1>HTML</h1>
        </Box>
        <Box data-aos="zoom-in-left">
          <img alt="img" src="https://chiranjeev-thapliyal.vercel.app/svg/css3.svg" />
          <h1>CSS</h1>
        </Box>
        <Box data-aos="zoom-in-right">
          <img alt="img" src="https://chiranjeev-thapliyal.vercel.app/svg/javascript.svg" />
          <h1>Javascript</h1>
        </Box>
        <Box data-aos="zoom-in-right">
          <img alt="img"
            style={{ borderRadius: "50%" }}
            src="https://velvety-croquembouche-9aebc1.netlify.app/static/media/typescript.f66836b8.svg"
          />
          <h1>Typescript</h1>
        </Box>
        <Box data-aos="zoom-in-left">
          <img alt="img" src="https://chiranjeev-thapliyal.vercel.app/svg/reactjs.svg" />
          <h1>React.js</h1>
        </Box>
        <Box data-aos="zoom-in-left">
          <img alt="img"
            style={{ borderRadius: "50%" }}
            src="https://seeklogo.com/images/N/next-js-logo-8FCFF51DD2-seeklogo.com.png"
          />
          <h1>Next.js</h1>
        </Box>
        <Box data-aos="zoom-in-right">
          <img alt="img" src="https://chiranjeev-thapliyal.vercel.app/svg/redux.svg" />
          <h1>Redux.js</h1>
        </Box>
        <Box data-aos="zoom-in-right">
          <img alt="img" src="https://chiranjeev-thapliyal.vercel.app/svg/nodejs.svg" />
          <h1>Node.js</h1>
        </Box>
        <Box data-aos="zoom-in-left">
          <img alt="img" src="https://chiranjeev-thapliyal.vercel.app/svg/mongodb.svg" />
          <h1>MongoDB</h1>
        </Box>
        <Box data-aos="zoom-in-left">
          <img alt="img"
            style={{ borderRadius: "50%" }}
            src="data:image/png;base64,"
          />
          <h1>Express.js</h1>
        </Box>
        <Box data-aos="zoom-in-right">
          <img alt="img"
            style={{ borderRadius: "50%" }}
            src="https://pbs.twimg.com/profile_images/1244925541448286208/rzylUjaf_400x400.jpg"
          />
          <h1>Chakra UI</h1>
        </Box>
        {/* <Box data-aos="zoom-in-right">
          <img alt="img"
            style={{ borderRadius: "50%" }}
            src="https://res.cloudinary.com/djo88dwrg/image/upload/v1677568819/mongoose_vekvmi.png"
          />
          <h1>Mongoose</h1>
        </Box> */}
      </div>
    </div>
    <Tools/>
    </div>
  );
};

export default Skills;
